a {
    font-family: "Fira Mono";
    color: white;
    margin: 10px;
}


.Table{
    align-items: center;
    justify-content: center;
}


.imageCell{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.Ratings {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
}

.MoviePoster {
    border-radius: 0%;
}
.PageTitle{
    color: white;
    font-family: "Fira Mono";
    font-size: 4vw;
    margin-left: 3vw;
    margin-top: 20px;
    text-align: left;
}

.Sort{
    margin-right: 3vw;
    display: inline-block;
}

.SortElement{
    color:whitesmoke;
    font-size: 1.3vw;
    margin: 5px;
}

.material-icons.SortIcon{
    color: white;
    font-size: 1.7vw;
}

.LikedHeader{
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.Divider{
    background-color: white;
    border-radius: 32px;
    width: 95%;
    height: 0.3vh;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    z-index: 0;
}

.DropdownSortMethods{
    position: absolute;
    display: none;
    background-color: white;
    border-radius: 10px;
    margin-right: 10px;
    right: 0px;
    padding: 10px;
    z-index: 1;
}

.SortMethodElement{
    font-size: 1vw;
    background: none;
    border: 0.8px solid transparent;
    text-align: right;
}

.material-icons{
    font-size: 1vw;
    background: none;
    border: none;
}

.SortIconBtn{
    background: none;
    border: none;
    display: flex;
}

.Sort:hover .DropdownSortMethods{
    display: flex;
    flex-direction: column;
}

.SortMethodElement:hover{
    border: 0.8px solid gray;

    border-radius: 12px;
}

h1, h2 {
    position: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 48px;
    font-family: "Libre Barcode 39 Text";
    color: white;
    margin: 10px;
}


.Error404 {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

/* отцентровать гифку */
.Error404 img {
    /* сделать гиф больше по размеру */
    width: 50%;
    height: 50%;
    display: block;
    margin: 0 auto;
}
#root{
    background-color: #272727;
    width: 100vw;
    height: 100vh;
}
